.tickets{
    padding: $gap 0;

    h3{
        text-align: center;
        color: $blanco;
    }
    .container-tickets{
        margin-top: $gap/2;

        @include grid(2,$gap);
    }
    .plan{
        p{
            color: $blanco;
            font-size: 2.5rem;
            text-align: center;
            margin-bottom: 2rem;
        }
    }
}
.pass{
    width: 100%;
    border-spacing: 0;

    thead{
        background-color: $rosa;
        th{
            padding: $gap/4;
            font-weight: $thin;
            font-size: 2rem;
            color: $blanco;
            text-transform: uppercase;
        }
    }
    tbody{
        background-color: $blanco;
        td{
            padding: $gap/4;
            border-bottom: 1px solid #e1e1e1;
        }
        tr{
            &:last-of-type{
                td{
                    border: none;
                }
            }
        }
    }
    tfoot{
        background-color: $morado;

        td{
            padding: $gap/4;
            text-align: center;
            font-size: 2.5rem;
            color: $blanco;
        }
    }
}
