.lineup{
    background-color: $rosa;
    padding: $gap 0;

    h3, p{
        color: $blanco;
        text-align: center;
        font-weight: $bold;
    }
    
    .day{
        text-transform: uppercase;
        margin: $gap 0;
        font-weight: $thin
    }

}
.container-scene{

    @include tablet{
        @include grid(2, $gap);
    }
    .scenes{
        padding-top: $gap/2;
    }
    .scene-name{
        color: $negro;
        font-size: 2rem;
        font-weight: $thin;

        &:after{
            content: '';
            width: 20rem;
            height: 10rem;
            display: block;
            margin: 2rem auto 0 auto;
            background-size: 16rem;
            background-repeat: no-repeat;
            background-position: center center;
        }
    }
}
.edn .scene-name{
    &:after{
    background-image: url('../img/icono_edm.png');
    }
}
.rock .scene-name{
    &:after{
    background-image: url('../img/icono_rock.png');
    }
}
.schedule{
    padding: 0;
    list-style: none;

    li{
        padding: $gap/2;
        font-weight: $bold;

        span{
            font-weight: $regular;
        }
    }
}
.bg-green .schedule li:nth-child(2n + 1){
    background-color: darken($verde, 10);
}
.bg-yellow .schedule li:nth-child(2n + 1){
    background-color: darken($amarillo, 20);
}