//Type of screens
@mixin cellphone {
    @media (min-width: $cellphone){
        @content;
    }
}
@mixin tablet {
    @media (min-width: $tablet){
        @content;
    }
}
@mixin desktop {
    @media (min-width: $desktop){
        @content;
    }
}

// Grids and Flexbox

@mixin grid($columns, $space) {
    display: grid;
    grid-template-columns: repeat($columns, 1fr);
    gap: $space;
}