.bg-yellow {
  background-color: #fef46d;
}

.bg-green {
  background-color: #4cb8b3;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

body.body-block {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000;
}

.container {
  max-width: 150rem;
  width: 90%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  margin: 0;
}

h1 {
  font-size: 3.6rem;
  font-weight: 900;
}

h2 {
  font-size: 3.2rem;
  font-weight: 700;
}

h3 {
  font-size: 2.8rem;
  font-weight: 400;
}

.header {
  background-color: #4cb8b3;
}

.header .container-header {
  padding: 1.66667rem 0;
}

@media (min-width: 488px) {
  .header .container-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.header h1 {
  margin-bottom: 1rem;
  color: #fff;
  text-align: center;
}

@media (min-width: 768px) {
  .header h1 {
    margin-bottom: 0;
  }
}

.header.attached {
  position: fixed;
  width: 100%;
  box-shadow: 0 .3rem .4rem;
}

.main-nav {
  display: flex;
  gap: 2rem;
  justify-content: center;
}

.main-nav a {
  color: #fff;
  font-size: 1.8rem;
}

.main-nav a:hover {
  color: #fef46d;
}

.video {
  position: relative;
  max-height: 50rem;
  overflow: hidden;
}

.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+74,ffffff+100&0.8+0,0.5+100 */
  background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.58) 74%, rgba(255, 255, 255, 0.5) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.58) 74%, rgba(255, 255, 255, 0.5) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.58) 74%, rgba(255, 255, 255, 0.5) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cc891c1e', endColorstr='#80ffffff',GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
}

.video video {
  display: block;
  width: 100%;
}

.content-video {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 1;
}

.content-video h2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: #fff;
  font-size: 6.5rem;
}

.content-video h2 span {
  background-color: #f53756;
  font-size: 2rem;
  padding: 1rem 2rem;
}

.festival-info {
  padding: 5rem 0;
}

@media (min-width: 768px) {
  .festival-info {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.festival-info__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 3rem;
}

.festival-info__content h2 {
  font-size: 3rem;
}

.festival-info__content .fecha {
  color: #f53756;
  font-weight: 700;
}

.festival-info__content .text {
  line-height: 1.5;
}

.lineup {
  background-color: #f53756;
  padding: 5rem 0;
}

.lineup h3, .lineup p {
  color: #fff;
  text-align: center;
  font-weight: 700;
}

.lineup .day {
  text-transform: uppercase;
  margin: 5rem 0;
  font-weight: 300;
}

@media (min-width: 768px) {
  .container-scene {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.container-scene .scenes {
  padding-top: 2.5rem;
}

.container-scene .scene-name {
  color: #000;
  font-size: 2rem;
  font-weight: 300;
}

.container-scene .scene-name:after {
  content: '';
  width: 20rem;
  height: 10rem;
  display: block;
  margin: 2rem auto 0 auto;
  background-size: 16rem;
  background-repeat: no-repeat;
  background-position: center center;
}

.edn .scene-name:after {
  background-image: url("../img/icono_edm.png");
}

.rock .scene-name:after {
  background-image: url("../img/icono_rock.png");
}

.schedule {
  padding: 0;
  list-style: none;
}

.schedule li {
  padding: 2.5rem;
  font-weight: 700;
}

.schedule li span {
  font-weight: 400;
}

.bg-green .schedule li:nth-child(2n + 1) {
  background-color: #3b9691;
}

.bg-yellow .schedule li:nth-child(2n + 1) {
  background-color: #fdec08;
}

.gallery {
  padding: 5rem 0;
}

.gallery h3 {
  text-align: center;
  margin-bottom: 5rem;
  font-weight: 700;
}

.gallery-img {
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2.5rem;
}

@media (min-width: 768px) {
  .gallery-img {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1.66667rem;
  }
}

@media (min-width: 1200px) {
  .gallery-img {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 1.66667rem;
  }
}

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center;
}

.btn-close {
  width: 5rem;
  height: 5rem;
  background-color: #fff;
  display: grid;
  place-items: center;
  font-weight: 900;
  font-size: 3rem;
  border-radius: 50%;
}

.btn-close:hover {
  cursor: pointer;
}

.tickets {
  padding: 5rem 0;
}

.tickets h3 {
  text-align: center;
  color: #fff;
}

.tickets .container-tickets {
  margin-top: 2.5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}

.tickets .plan p {
  color: #fff;
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 2rem;
}

.pass {
  width: 100%;
  border-spacing: 0;
}

.pass thead {
  background-color: #f53756;
}

.pass thead th {
  padding: 1.25rem;
  font-weight: 300;
  font-size: 2rem;
  color: #fff;
  text-transform: uppercase;
}

.pass tbody {
  background-color: #fff;
}

.pass tbody td {
  padding: 1.25rem;
  border-bottom: 1px solid #e1e1e1;
}

.pass tbody tr:last-of-type td {
  border: none;
}

.pass tfoot {
  background-color: #752f97;
}

.pass tfoot td {
  padding: 1.25rem;
  text-align: center;
  font-size: 2.5rem;
  color: #fff;
}

.footer {
  background-color: #f53756;
  padding: 1rem;
  text-align: center;
}

.footer p {
  color: #fff;
  margin: 0;
}
