.header {
    background-color: $verde;

    .container-header{
        padding: $gap/3 0;
        @include cellphone{
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
    h1{
        margin-bottom: 1rem;
        color: $blanco;
        text-align: center;
        @include tablet{
            margin-bottom: 0
        };
    }
    &.attached{
        position: fixed;
        width: 100%;
        box-shadow: 0 .3rem .4rem;
    }   
}
.main-nav{
    display: flex;
    gap: 2rem;
    justify-content: center;

    a{
        color: $blanco;
        font-size: 1.8rem;

        &:hover{
            color: $amarillo;
        }
    }   
}