.festival-info{
    padding: $gap 0;

    @include tablet{
        @include grid(2, $gap);
        }
}

.festival-info__content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 3rem;

    h2{
        font-size: 3rem;
    }
    .fecha{
        color: $rosa;
        font-weight: $bold;
    }
    .text{
        line-height: 1.5;
    }
}