.gallery{
    padding: $gap 0;

    h3{
        text-align: center;
        margin-bottom: $gap;
        font-weight: $bold ;
    }
}
.gallery-img{
    list-style: none;
    padding: 0;

    @include grid(2, $gap/2);

    @include tablet{
        @include grid(4, $gap/3)
    }
    @include desktop{
        @include grid(6, $gap/3)
    }
}

.overlay{
    position: fixed;
    background-color: rgba(0,0,0,.5);
    width: 100%;
    height: 100%;
    top: 0;
    display: grid;
    place-items: center;
}
.btn-close{
    width: $gap;
    height: $gap;
    background-color: $blanco;
    display: grid;
    place-items: center;
    font-weight: $black;
    font-size: 3rem;
    border-radius: 50%;

    &:hover{
        cursor: pointer;
    }
}