html{
     font-size: 62.5%;
      box-sizing: border-box;
}
*, *:before, *:after{
     box-sizing: inherit;
}
body{
    font-family: $fuentePrincipal;
    font-size: 1.6rem;

    &.body-block{
        overflow: hidden;
    }
}
p{
    font-size: 2rem;
    color: $negro;
}
.container{
    max-width: 150rem;
    width: 90%;
    margin: 0 auto;
}
a {
    text-decoration: none;
}
img {
    max-width: 100%;
}
h1, h2, h3 {
    margin: 0;
}
h1{
    font-size: 3.6rem;
    font-weight: $black;
}
h2{
    font-size: 3.2rem;    
    font-weight: $bold;
}
h3{
    font-size: 2.8rem;    
    font-weight: $regular;
}